      </l-control>

      <!--<l-control
        :position="'topright'"
        class="custom-control-watermark"
      >
        <section>
        <b-collapse
          v-if="showPanel"
          v-model="isOpen"
        >          
          <div>
            <v-card
              elevation="2"
