<script>

import { latLng, icon } from "leaflet";
import { LMap, LTileLayer, LControl, LPolyline, /*LPolygon,*/ LTooltip} from "vue2-leaflet";
import axios from 'axios'
import Vue2LeafletRotatedMarker from 'vue2-leaflet-rotatedmarker'
//import Vue2LeafletMarkerCluster from 'vue2-leaflet-markercluster'

import { Icon } from 'leaflet';

delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

export default {
  name: "Example",
  components: {
    LMap,
    LTileLayer,
    LControl,
    //LPolygon,
    LPolyline,
    LTooltip,
    'v-rotated-marker': Vue2LeafletRotatedMarker
    //'v-marker-cluster': Vue2LeafletMarkerCluster
  },
  data() {
    return {

      bplaClusterOptions: {
        removeOutsideVisibleBounds: true,
        showCoverageOnHover: false
      },

      rules: [
        value => !!value || 'Обязательно для заполнения',
      ],

      ordersTrack: [],
      ordersTrackPoints: [],
      input_OrderId: "",
      tryingOrderTrack: false,

      isOpen: true,

      appStart: true,
      appStartError: "",
      tryingConnect: false,
      connectError: false,

      apiUrlInput: "http://26.77.72.147:8080/api",
      apiUrl: 'http://26.77.72.147:8080/api',
      apiError: false,
      apiErrorMsg: "Подключение к серверу API...",

      panelAddOrderShow: false,
      panelTrackOrderShow: false,

      showPanel: false,

      panelType: "",
      panelText: "",
      panelHubId: "",
      panelHeading: "",
      panelHeadingIcon: "",
      panelTextType: " ",

      createOrderData: {},
      first_hub: "",
      last_hub: "",
      weight: "",
      createOrderSuccess: false,
      createOrderFault: false,
      tryingCreateOrder: false,
      createOrderId: "",

      bpla_apidata: [],
      hubs_buffer: null,
      hubs_apidata: [],
      
      bplaTrack: [],
      bplaOrdersOnboard: [],

      speedToggle: 2,

      statistics: {
        "weight": 0,
        "distance": 0,
        "uavs": 0
      },
      
      zoom: 10,
      center: latLng(56.010569, 92.852572),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        'Изпользуется карта &copy; <a href="http://osm.org/copyright">OpenStreetMap</a>.',

      iconBpla: icon({
        iconUrl: "airplane.svg",
        iconSize: [30, 30],
        iconAnchor: [15, 15]
      }),

      iconBplaWhite: icon({
        iconUrl: "airplane_white.svg",
        iconSize: [40, 40],
        iconAnchor: [20, 20]
      }),

      iconDron: icon({
        iconUrl: "quadcopter.svg",
        iconSize: [20, 20],
        iconAnchor: [10, 10]
      }),

      iconHubLarge: icon({
        iconUrl: "black.svg",
        iconSize: [50, 50],
        iconAnchor: [25, 25]
      }),

      iconHubMedium: icon({
        iconUrl: "blue.svg",
        iconSize: [30, 30],
        iconAnchor: [15, 15]
      }),

      iconHubSmall: icon({
        iconUrl: "green.svg",
        iconSize: [20, 20],
        iconAnchor: [10, 10]
      }),

      iconSize: 64,

      orderresp: ""
    };
  },
  methods: {

    connectApiServer() {
      this.apiUrl = this.apiUrlInput;
      this.tryingConnect = true;
      this.connectError = false;
      
      axios
        .get(this.apiUrl + '/hubs', { timeout: 5000 })
        .then((response) => { 
          console.log("OKOKOK" + response);
          this.apiUrl = this.apiUrlInput;
          this.appStart = false;
          this.tryingConnect = false;
        })
        .catch(error => {
          console.log("ERROR" + error);
          this.connectError = true;
          this.tryingConnect = false;
        })
        .finally(() => (this.loading = false));

      this.callBplaApi();
      this.callHubsApi();
    },

    // вызов API 

    callBplaApi() {

      //console.log(bplaTime);
        axios
          .get(this.apiUrl + '/drones', { timeout: 2000 })
          .then((response) => { 
            this.bpla_apidata = response;
            this.apiError = false;
          })
          .catch(error => {
            console.log(error);
            this.apiError = true;
          })
          .finally(console.log(this.bpla_apidata), () => (this.loading = false));

    }, 

    smoothDrawBpla() 
    {
      this.bpla_apidata.data.drones.forEach(bpla => 
      {
        /*let s =  bpla.speed * (1 / (60 * 60 * 10000));
          
        bpla.lat = Number.parseFloat(bpla.lat) + (s * Math.cos(bpla.azimuth * Math.PI / 180));
        bpla.lon = Number.parseFloat(bpla.lon) + (s * Math.sin(bpla.azimuth * Math.PI / 180));*/
        
        let d_lat = (bpla.lat_delta / 50);
        let d_lon = (bpla.long_delta / 50); 

        if(bpla.type == "0") 
        {
          if(!(Math.abs(bpla.destination_hub_lat - bpla.lat) < 0.0001 || Math.abs(bpla.destination_hub_long - bpla.lon) < 0.0001)) 
          {      
            bpla.lat = Number.parseFloat(bpla.lat) + d_lat;
            bpla.lon = Number.parseFloat(bpla.lon) + d_lon;
            console.log("KRKRKRK");
          }
        }
        else if(bpla.type == "1") 
        {
          if(!(Math.abs(bpla.destination_hub_lat - bpla.lat) < 0.001 || Math.abs(bpla.destination_hub_long - bpla.lon) < 0.001)) 
          {      
            bpla.lat = Number.parseFloat(bpla.lat) + d_lat;
            bpla.lon = Number.parseFloat(bpla.lon) + d_lon;
            console.log("KRKRKRK");
          }
        }
        else if(bpla.type == "2") 
        {
          if(!(Math.abs(bpla.destination_hub_lat - bpla.lat) < 0.01 || Math.abs(bpla.destination_hub_long - bpla.lon) < 0.01)) 
          {      
            bpla.lat = Number.parseFloat(bpla.lat) + d_lat;
            bpla.lon = Number.parseFloat(bpla.lon) + d_lon;
            console.log("KRKRKRK");
          }
        }

        //console.log(Math.abs(bpla.destination_hub_lat - bpla.lat));
      });
    },

    callHubsApi(){
        axios
          .get(this.apiUrl + '/hubs', { timeout: 2000 })
          .then((response) => {
            this.hubs_apidata = response.data;
            this.apiError = false;
          })
          .catch(error => {
            console.log(error);
            this.apiError = true;
          })
          .finally(() => (this.loading = false));
    },

    displayBplaInfo(bpla_info)
    {
      this.panelType = "bpla";
      this.panelText = bpla_info.id;
      this.panelHeading = "БпЛА";

      if(bpla_info.type == "0") {
        this.panelHeadingIcon = "mdi-quadcopter";
        this.panelHeading = "Малый БпЛА";
      }
      else if(bpla_info.type == "1") {
        this.panelHeadingIcon = "mdi-airplane";
        this.panelHeading = "Средний БпЛА";
      }
      else if(bpla_info.type == "2") {
        this.panelHeadingIcon = "mdi-airplane";
        this.panelHeading = "Большой БпЛА";
      }
      this.panelTextType = "Номер";
      this.showPanel = true;

      this.displayBplaTrack(bpla_info.id);
      this.callBplaOrdersOnboard(bpla_info.id);
      
      //this.panelText = this.bplaTrack;//bpla_info.id;
    },

    /*testBplaTrack()
    {      

      this.bplaTrack = [[62.021029 + Math.random(), 129.729444], [61.994661 + Math.random(), 129.689985]];
    },*/

    displayBplaTrack(bplaId) 
    {
      this.ordersTrackPoints = [];
      axios
        .get(this.apiUrl + '/drones/' + bplaId + '/track')
        .then((response) => {
          console.log(response.data);
          this.apiError = false;
          //this.bplaTrack = response.data;
          /*this.bplaTrack[0] = response.data.departure_hub;
          this.bplaTrack[1] = response.data.destination_hub;*/
          this.bplaTrack = [response.data.departure_hub, response.data.destination_hub];
        })
        .catch(error => {
          console.log(error);
          this.apiError = true;
        })
        .finally(() => (this.loading = false));
    },

    callBplaOrdersOnboard(bplaId) 
    {
      axios
        .get(this.apiUrl + '/drones/' + bplaId + '/orders')
        .then((response) => {
          console.log(response.data);
          this.apiError = false;
          this.bplaOrdersOnboard = response.data;
        })
        .catch(error => {
          console.log(error);
          this.apiError = true;
        })
        .finally(() => (this.loading = false));
    },

    displayOrderTrack(orderId) 
    {
      this.bplaTrack = [];

      this.tryingOrderTrack = true,

      axios
        .get(this.apiUrl + '/orders/' + orderId + '/track')
        .then((response) => {
          console.log(response.data);
          this.apiError = false;
          this.ordersTrack = response.data.track.Product_path;
          this.tryingOrderTrack = false;

          this.ordersTrackPoints = [];
          let nof_TrackPoints = 0;
          this.ordersTrack.forEach(() => {
            this.ordersTrackPoints[nof_TrackPoints] = this.ordersTrack[nof_TrackPoints].Latlong;
            console.log(JSON.stringify(this.ordersTrack[nof_TrackPoints].Latlong));
            nof_TrackPoints++; 
          });
        })
        .catch(error => {
          console.log(error);
          this.apiError = true;
          this.tryingOrderTrack = false;
        })
        .finally(() => (this.loading = false));
    },

    displayHubInfo(hub){

      this.panelType = "hub";
      this.panelHeading = "Хаб";
      this.panelHubId = hub.id;
      this.panelTextType = "Координаты";
      this.panelText = hub.latitude + ", " + hub.longitude;

      if(hub.type == "0") {
        this.panelHeading = "Хаб малый";
      }
      else if(hub.type == "1") {
        this.panelHeading = "Хаб средний";
      }
      else if(hub.type == "2") {
        this.panelHeading = "Хаб большой";
      }
      this.panelHeadingIcon = "mdi-home-map-marker";
      //this.panelTextType = "Тип";
      this.showPanel = true;
    },

    createNewOrder() {
      this.createOrderSuccess = false;
      this.createOrderFault = false;
      this.tryingCreateOrder = true;
      this.createOrderId = "";

      this.createOrderData["weight"] = this.weight;
      this.createOrderData["first_hub"] = this.first_hub;
      this.createOrderData["last_hub"] = this.last_hub;

      axios.post(this.apiUrl + '/orders', JSON.stringify(this.createOrderData), { timeout: 5000 })
        .then((res) => {
          this.orderresp = res.data;
          if(res.data.result == "success") {
            this.createOrderSuccess = true;
            this.createOrderId = res.data.id;
          }
          else {
            this.createOrderFault = true;
          }
          this.tryingCreateOrder = false;
          this.orderresp = res;
        })
        .catch((error) => {
          // eslint-отключение следующей строки
          console.error("ERROR" + error);
          this.createOrderFault = true;
          this.tryingCreateOrder = false;
        });
    },

    displayStats() 
    {
      axios
        .get(this.apiUrl + '/statistics')
        .then((response) => {
          console.log(response.data);
          this.apiError = false;
          this.statistics = response.data;
        })
        .catch(error => {
          console.log(error);
          this.apiError = true;
        })
        .finally(() => (this.loading = false));
    },
    
    closeCreateOrderCard()
    {
      this.panelAddOrderShow = false; 
      this.createOrderSuccess = false;
      this.createOrderFault = false;
    },

    cleanViewspace() {
      this.panelText = " ";
      this.panelHeading = " ";
      this.panelTextType = " ";
      this.showPanel = false;

      this.panelAddOrderShow = false;
      this.panelTrackOrderShow = false;

      this.bplaTrack = [];
      this.ordersTrackPoints = [];

      this.closeCreateOrderCard();
    }

  },
  mounted() 
  {
    this.callBplaApi();
    this.callHubsApi();
    this.displayStats();

    setInterval(function () {
      this.smoothDrawBpla();
    }.bind(this), 50);  
    
    setInterval(function () {
      this.displayStats();
    }.bind(this), 5000); 

    setInterval(function () {
      this.callBplaApi();
    }.bind(this), 10000);

    setInterval(function () {
      this.callHubsApi();
    }.bind(this), 1000 * 60 * 60);
  }
};

</script>

